import React, { Component, useState } from 'react'
import axios from 'axios'

import { Button, Modal,  }  from 'react-bootstrap';
import CarrocelImg from './Carrocel'
import CIcon from '@coreui/icons-react'
import {cilShieldAlt, cilCamera, cilLoopCircular } from '@coreui/icons'

import { BsFillCameraFill } from 'react-icons/bs';

import Cookies from 'js-cookie'
import {CButton} from '@coreui/react'

const Midia =  (propsId) => {

    const [visible, setVisible] = useState(false)  
    const [show, setShow] = useState(false);
    const handleClose = () => setShow(false);

    const userType = Cookies.get('userType')
    const token = Cookies.get('TokenID') 
    const urlDomain = 'http://dashboardbff.oonpayperuse.com.br' //'http://localhost:8080'

    let config = {
      headers: {
        'Authorization': token, 
        'Content-Type': 'application/json;charset=UTF-8',
        "Access-Control-Allow-Origin": "*",
      }
    };

    const [data, setData] = useState()

    function verifica(data){
        axios 
            .get(`${urlDomain}/images/vehicle/${propsId.midia}`, config)
            .then((response) => {
                setData(response.data)
                const handleShow =  setShow(true);
             })
            .catch(r =>{ 
                console.log('error', r), alert('Login expirado'),window.location.reload()
            })

    }

    function salvarEdicoes (){
        setShow(false);
    }
    return (
        <>
            
            <CButton  onClick={() => verifica(propsId)} size='md' className='icon-css'  color="secondary" >
                <BsFillCameraFill />
            </CButton>
            
            <Modal 
                show={show} 
                onHide={handleClose}
                size="xl"
                
            >
                <Modal.Header  closeButton>
                    <Modal.Title>Fotos do Veículo </Modal.Title>
                </Modal.Header>
                <Modal.Body className='container-body'>
                    <CarrocelImg midia={data} />
                </Modal.Body>

                    
                
                <Modal.Footer>
                    <Button variant="secondary" onClick={handleClose}>
                        Sair
                    </Button>
                </Modal.Footer>
                
            </Modal>


        </>
    )
}


export default Midia